<template>
  <v-app-bar
    v-if="logado"
    id="default-app-bar"
    app
    absolute
    class="v-bar--underline"
    color="transparent"
    :clipped-left="$vuetify.rtl"
    :clipped-right="!$vuetify.rtl"
    height="70"
    flat
  >
    <v-app-bar-nav-icon
      class="hidden-md-and-up"
      @click="drawer = !drawer"
    />

    <default-drawer-toggle class="hidden-sm-and-down" />

    <v-toolbar-title
      class="font-weight-light text-h4"
      v-text="'Bem Vindo(a), '+ user"
    />

    <v-spacer />

    <licences-notifications />

    <expired-notifications />

    <soon-expiration-notifications />

    <default-account />
  </v-app-bar>
</template>

<script>
  import { get, sync } from 'vuex-pathify'

  export default {
    name: 'DefaultBar',

    components: {
      DefaultAccount: () => import( './widgets/Account' ),
      DefaultDrawerToggle: () => import( './widgets/DrawerToggle' ),
      ExpiredNotifications: () => import( './widgets/ExpiredNotifications' ),
      LicencesNotifications: () => import( './widgets/LicencesNotifications' ),
      SoonExpirationNotifications: () => import( './widgets/SoonExpirationNotifications' )
    },

    data: () => ({
      user: window.sessionStorage.user,
      logado: window.sessionStorage.logado === 'true',
    }),

    computed: {
      ...sync('app', [
        'drawer',
        'mini',
      ]),
      name: get('route/name'),
    },
  }
</script>
